import React, { useEffect, useState,  } from "react";
import { Link, useNavigate } from "react-router-dom";
import { format } from "date-fns";
import * as XLSX from 'xlsx';
import 'jspdf-autotable';
import "./Attendance.css";

const API = "http://localhost:5000/api";

export default function AttendanceDashboard() {
  const [students, setStudents] = useState([]);
  const [filteredStudents, setFilteredStudents] = useState([]);
  const [attendance, setAttendance] = useState({});
  const [selectedDate, setSelectedDate] = useState(format(new Date(), "yyyy-MM-dd"));
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");
  const [searchTerm, setSearchTerm] = useState("");
  const [draggedItem, setDraggedItem] = useState(null);
  const navigate = useNavigate();

  const getAuthHeaders = () => {
    const token = localStorage.getItem("authToken");
    return token
      ? { "Content-Type": "application/json", Authorization: `Bearer ${token}` }
      : {};
  };

  // Filter students based on search term
  useEffect(() => {
    const filtered = students.filter(student => {
      const nameMatch = (student.name || "").includes(searchTerm);
      const idMatch = (student.studentId || "").includes(searchTerm);
      return nameMatch || idMatch;
    });    
    setFilteredStudents(filtered);
  }, [searchTerm, students]);

  useEffect(() => {
    const fetchStudents = async () => {
      try {
        const res = await fetch(`${API}/students`, {
          method: "GET",
          headers: getAuthHeaders(),
        });

        if (res.status === 401) {
          setError("Unauthorized! Please log in again.");
          return navigate("/auth");
        }

        const data = await res.json();
        if (data.length === 0) {
          setError("No students found.");
        } else {
          setStudents(data);
          setFilteredStudents(data); // Initialize filtered students
        }
      } catch (err) {
        console.error("Error fetching students:", err);
        setError("Failed to fetch students.");
      }
    };

    const fetchAttendance = async () => {
      try {
        const res = await fetch(`${API}/attendance?date=${selectedDate}`, {
          method: "GET",
          headers: getAuthHeaders(),
        });

        if (res.status === 401) {
          return navigate("/auth");
        }

        const contentType = res.headers.get("Content-Type");
        if (contentType && contentType.includes("application/json")) {
          const data = await res.json();
          const attendanceMap = {};
          data.attendance.forEach((entry) => {
            attendanceMap[entry.student._id] = entry.status;
          });
          setAttendance(attendanceMap);
        } else {
          const text = await res.text();
          console.error("Received unexpected response:", text);
          setError("Unexpected error. Please try again.");
        }
      } catch (err) {
        console.error("Error fetching attendance:", err);
        setError("Failed to fetch attendance.");
      }
    };

    fetchStudents();
    fetchAttendance();
  }, [selectedDate, navigate]);

  const markAttendance = async (studentId, status) => {
    try {
      const res = await fetch(`${API}/attendance`, {
        method: "POST",
        headers: getAuthHeaders(),
        body: JSON.stringify({ studentId, status, date: selectedDate }),
      });

      const contentType = res.headers.get("Content-Type");

      if (contentType && contentType.includes("application/json")) {
        const data = await res.json();

        if (res.ok) {
          setAttendance((prev) => ({ ...prev, [studentId]: status }));
          setMessage(`Marked ${status}`);
          setTimeout(() => setMessage(""), 2000);
        } else {
          throw new Error(data.message || "Failed to mark attendance.");
        }
      } else {
        const text = await res.text();
        throw new Error(`Unexpected response: ${text}`);
      }
    } catch (err) {
      console.error("Error:", err);
      setError("Failed to mark attendance.");
    }
  };

  // Drag and Drop functionality
  const handleDragStart = (e, index) => {
    setDraggedItem(filteredStudents[index]);
    e.dataTransfer.effectAllowed = "move";
    e.dataTransfer.setData("text/html", e.target.parentNode);
  };

  const handleDragOver = (index) => {
    if (draggedItem === null) return;
    
    const draggedOverItem = filteredStudents[index];
    if (draggedItem === draggedOverItem) return;

    const items = [...filteredStudents];
    const draggedItemIndex = items.indexOf(draggedItem);
    items.splice(draggedItemIndex, 1);
    items.splice(index, 0, draggedItem);
    
    setFilteredStudents(items);
  };

  const handleDragEnd = () => {
    setDraggedItem(null);
  };

  // Download functionality
  const downloadExcel = () => {
    const data = filteredStudents.map(student => ({
      "S.No": filteredStudents.indexOf(student) + 1,
      "Name": student.name,
      "Register Number": student.studentId,
      "Status": attendance[student._id] || "Not marked",
      "Date": selectedDate
    }));

    const worksheet = XLSX.utils.json_to_sheet(data);
    const workbook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(workbook, worksheet, "Attendance");
    XLSX.writeFile(workbook, `Attendance_${selectedDate}.xlsx`);
  };


  return (
    <div className="dashboard">
      <Link to="/dashboard"> Dashboard</Link>
      <h2> Attendance Dashboard</h2>

      <div className="controls">
        <div className="date-picker">
          <label>Select Date:</label>
          <input
            type="date"
            value={selectedDate}
            onChange={(e) => setSelectedDate(e.target.value)}
          />
        </div>

        <div className="search-box">
          <input
            type="text"
            placeholder="Search by name or ID..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        <div className="download-buttons">
          <button onClick={downloadExcel}>Download Excel</button>
        </div>
      </div>

      {error && <p className="status-message error">{error}</p>}
      {message && <p className="status-message">{message}</p>}
      {filteredStudents.length === 0 && !error && <p>No students found.</p>}

      <table className="attendance-table">
        <thead>
          <tr>
            <th>#</th>
            <th>Name</th>
            <th>Status</th>
            <th>Mark</th>
          </tr>
        </thead>
        <tbody>
          {filteredStudents.map((student, idx) => (
            <tr 
              key={student._id}
              draggable
              onDragStart={(e) => handleDragStart(e, idx)}
              onDragOver={() => handleDragOver(idx)}
              onDragEnd={handleDragEnd}
              className={draggedItem === student ? "dragging" : ""}
            >
              <td>{idx + 1}</td>
              <td>{student.name}</td>
              <td>{attendance[student._id] || "Not marked"}</td>
              <td>
                <button
                  onClick={() => markAttendance(student._id, "present")}
                  disabled={attendance[student._id] === "present"}
                >
                  
                </button>
                <button
                  onClick={() => markAttendance(student._id, "absent")}
                  disabled={attendance[student._id] === "absent"}
                >
                  
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}